// app/admin/page.js (‡∏´‡∏£‡∏∑‡∏≠‡∏´‡∏ô‡πâ‡∏≤‡∏ó‡∏µ‡πà‡∏ï‡πâ‡∏≠‡∏á‡∏Å‡∏≤‡∏£)
'use client';
import { useState, useEffect } from 'react';
import {
  getFirestore, collection, addDoc, getDocs, doc, deleteDoc,
  updateDoc, serverTimestamp, query, orderBy
} from "firebase/firestore";
import { db } from '@/lib/firebase'; // *** ‡∏ï‡∏£‡∏ß‡∏à‡∏™‡∏≠‡∏ö path ‡∏ô‡∏µ‡πâ‡πÉ‡∏´‡πâ‡∏ñ‡∏π‡∏Å‡∏ï‡πâ‡∏≠‡∏á ***

export default function AdminPage() {
  const [songs, setSongs] = useState([]);
  // --- 1. ‡πÄ‡∏û‡∏¥‡πà‡∏° State ‡∏Ç‡∏≠‡∏á Form ---
  const [form, setForm] = useState({
    bandName: '',
    albumTitle: '',
    songTitle: '',
    youtubeLink: '',
    albumId: '',       // <--- ‡πÄ‡∏û‡∏¥‡πà‡∏° albumId
    trackNumber: ''    // <--- ‡πÄ‡∏û‡∏¥‡πà‡∏° trackNumber (‡πÉ‡∏ä‡πâ string ‡∏™‡∏≥‡∏´‡∏£‡∏±‡∏ö input)
  });
  const [editId, setEditId] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchSongs();
  }, []);

  const fetchSongs = async () => {
    try {
      setLoading(true);
      const songsCollection = collection(db, "songs");
      const q = query(songsCollection, orderBy("createdAt", "desc"));
      const songsSnapshot = await getDocs(q);
      const songsList = songsSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      setSongs(songsList);
      setError(null);
    } catch (err) {
      console.error("Error fetching songs:", err);
      setError("‡πÑ‡∏°‡πà‡∏™‡∏≤‡∏°‡∏≤‡∏£‡∏ñ‡πÇ‡∏´‡∏•‡∏î‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÄ‡∏û‡∏•‡∏á‡πÑ‡∏î‡πâ");
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleAdd = async () => {
    // --- 2. ‡∏õ‡∏£‡∏±‡∏ö Validation ---
    if (!form.bandName || !form.albumTitle || !form.songTitle || !form.youtubeLink || !form.albumId || !form.trackNumber) {
        setError("‡∏Å‡∏£‡∏∏‡∏ì‡∏≤‡∏Å‡∏£‡∏≠‡∏Å‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÉ‡∏´‡πâ‡∏Ñ‡∏£‡∏ö‡∏ó‡∏∏‡∏Å‡∏ä‡πà‡∏≠‡∏á (‡∏£‡∏ß‡∏° Album ID ‡πÅ‡∏•‡∏∞ Track Number)");
        return;
    }
    // --- ‡πÄ‡∏û‡∏¥‡πà‡∏° Validation ‡∏™‡∏≥‡∏´‡∏£‡∏±‡∏ö trackNumber ---
    const trackNum = parseInt(form.trackNumber, 10);
    if (isNaN(trackNum) || trackNum <= 0) {
        setError("Track Number ‡∏ï‡πâ‡∏≠‡∏á‡πÄ‡∏õ‡πá‡∏ô‡∏ï‡∏±‡∏ß‡πÄ‡∏•‡∏Ç‡∏ó‡∏µ‡πà‡∏°‡∏≤‡∏Å‡∏Å‡∏ß‡πà‡∏≤ 0");
        return;
    }

    try {
      setLoading(true);
      // --- 3. ‡∏õ‡∏£‡∏±‡∏ö Field ‡∏ó‡∏µ‡πà‡∏à‡∏∞ Add ---
      const docData = {
        bandName: form.bandName.trim(),
        albumTitle: form.albumTitle.trim(),
        songTitle: form.songTitle.trim(),
        youtubeLink: form.youtubeLink.trim(),
        albumId: form.albumId.trim(),         // <--- ‡πÄ‡∏û‡∏¥‡πà‡∏° albumId
        trackNumber: trackNum,                // <--- ‡πÄ‡∏û‡∏¥‡πà‡∏° trackNumber (‡πÄ‡∏õ‡πá‡∏ô Number)
        createdAt: serverTimestamp()
      };
      const docRef = await addDoc(collection(db, "songs"), docData);

      // Update state immediately
       setSongs([
         {
           id: docRef.id,
           ...docData, // ‡πÉ‡∏ä‡πâ‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏ó‡∏µ‡πà‡πÄ‡∏ï‡∏£‡∏µ‡∏¢‡∏°‡πÑ‡∏ß‡πâ
           createdAt: new Date() // ‡πÉ‡∏ä‡πâ Date ‡∏õ‡∏±‡∏à‡∏à‡∏∏‡∏ö‡∏±‡∏ô‡∏™‡∏≥‡∏´‡∏£‡∏±‡∏ö‡πÅ‡∏™‡∏î‡∏á‡∏ú‡∏•‡∏ä‡∏±‡πà‡∏ß‡∏Ñ‡∏£‡∏≤‡∏ß
         },
         ...songs
       ]);

      // --- 4. ‡∏õ‡∏£‡∏±‡∏ö‡∏Å‡∏≤‡∏£ Reset Form ---
      setForm({ bandName: '', albumTitle: '', songTitle: '', youtubeLink: '', albumId: '', trackNumber: '' });
      setError(null);
    } catch (err) {
      console.error("Error adding song:", err);
      setError("‡πÑ‡∏°‡πà‡∏™‡∏≤‡∏°‡∏≤‡∏£‡∏ñ‡πÄ‡∏û‡∏¥‡πà‡∏°‡πÄ‡∏û‡∏•‡∏á‡πÑ‡∏î‡πâ");
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async (id) => {
    // ... (‡πÇ‡∏Ñ‡πâ‡∏î handleDelete ‡πÄ‡∏´‡∏°‡∏∑‡∏≠‡∏ô‡πÄ‡∏î‡∏¥‡∏°) ...
    if (!confirm(`‡∏ï‡πâ‡∏≠‡∏á‡∏Å‡∏≤‡∏£‡∏•‡∏ö‡πÄ‡∏û‡∏•‡∏á ID: ${id} ‡πÉ‡∏ä‡πà‡∏´‡∏£‡∏∑‡∏≠‡πÑ‡∏°‡πà?`)) return;
    try {
        setLoading(true);
        await deleteDoc(doc(db, "songs", id));
        setSongs(songs.filter((song) => song.id !== id));
        setError(null);
      } catch (err) {
        console.error("Error deleting song:", err);
        setError("‡πÑ‡∏°‡πà‡∏™‡∏≤‡∏°‡∏≤‡∏£‡∏ñ‡∏•‡∏ö‡πÄ‡∏û‡∏•‡∏á‡πÑ‡∏î‡πâ");
      } finally {
        setLoading(false);
      }
  };

  const handleEdit = (song) => {
    setEditId(song.id);
    // --- 5. ‡∏õ‡∏£‡∏±‡∏ö‡∏Å‡∏≤‡∏£ Set Form ‡∏ï‡∏≠‡∏ô Edit ---
    setForm({
      bandName: song.bandName || '',
      albumTitle: song.albumTitle || '',
      songTitle: song.songTitle || '',
      youtubeLink: song.youtubeLink || '',
      albumId: song.albumId || '', // <--- ‡πÄ‡∏û‡∏¥‡πà‡∏° albumId
      trackNumber: song.trackNumber !== undefined ? String(song.trackNumber) : '', // <--- ‡πÄ‡∏û‡∏¥‡πà‡∏° trackNumber (‡πÅ‡∏õ‡∏•‡∏á‡πÄ‡∏õ‡πá‡∏ô String)
    });
  };

  const handleUpdate = async () => {
    // --- 6. ‡∏õ‡∏£‡∏±‡∏ö Validation ---
     if (!form.bandName || !form.albumTitle || !form.songTitle || !form.youtubeLink || !form.albumId || !form.trackNumber) {
        setError("‡∏Å‡∏£‡∏∏‡∏ì‡∏≤‡∏Å‡∏£‡∏≠‡∏Å‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÉ‡∏´‡πâ‡∏Ñ‡∏£‡∏ö‡∏ó‡∏∏‡∏Å‡∏ä‡πà‡∏≠‡∏á (‡∏£‡∏ß‡∏° Album ID ‡πÅ‡∏•‡∏∞ Track Number)");
        return;
    }
    const trackNum = parseInt(form.trackNumber, 10);
    if (isNaN(trackNum) || trackNum <= 0) {
        setError("Track Number ‡∏ï‡πâ‡∏≠‡∏á‡πÄ‡∏õ‡πá‡∏ô‡∏ï‡∏±‡∏ß‡πÄ‡∏•‡∏Ç‡∏ó‡∏µ‡πà‡∏°‡∏≤‡∏Å‡∏Å‡∏ß‡πà‡∏≤ 0");
        return;
    }
    if (!editId) return;

    try {
      setLoading(true);
      const songRef = doc(db, "songs", editId);
      // --- 7. ‡∏õ‡∏£‡∏±‡∏ö Field ‡∏ó‡∏µ‡πà‡∏à‡∏∞ Update ---
      await updateDoc(songRef, {
        bandName: form.bandName.trim(),
        albumTitle: form.albumTitle.trim(),
        songTitle: form.songTitle.trim(),
        youtubeLink: form.youtubeLink.trim(),
        albumId: form.albumId.trim(),         // <--- ‡πÄ‡∏û‡∏¥‡πà‡∏° albumId
        trackNumber: trackNum,                // <--- ‡πÄ‡∏û‡∏¥‡πà‡∏° trackNumber (‡πÄ‡∏õ‡πá‡∏ô Number)
        updatedAt: serverTimestamp()
      });

      // Update state
      setSongs(
        songs.map((song) =>
          song.id === editId ? {
            ...song,
            bandName: form.bandName.trim(),
            albumTitle: form.albumTitle.trim(),
            songTitle: form.songTitle.trim(),
            youtubeLink: form.youtubeLink.trim(),
            albumId: form.albumId.trim(),     // <--- ‡πÄ‡∏û‡∏¥‡πà‡∏° albumId
            trackNumber: trackNum,            // <--- ‡πÄ‡∏û‡∏¥‡πà‡∏° trackNumber
           } : song
        )
      );

      // --- 8. ‡∏õ‡∏£‡∏±‡∏ö‡∏Å‡∏≤‡∏£ Reset Form ---
      handleCancelEdit(); // ‡πÉ‡∏ä‡πâ‡∏ü‡∏±‡∏á‡∏Å‡πå‡∏ä‡∏±‡∏ô cancel ‡πÄ‡∏û‡∏∑‡πà‡∏≠ reset ‡πÅ‡∏•‡∏∞‡πÄ‡∏Ñ‡∏•‡∏µ‡∏¢‡∏£‡πå error
    } catch (err) {
      console.error("Error updating song:", err);
      setError("‡πÑ‡∏°‡πà‡∏™‡∏≤‡∏°‡∏≤‡∏£‡∏ñ‡∏≠‡∏±‡∏û‡πÄ‡∏î‡∏ï‡πÄ‡∏û‡∏•‡∏á‡πÑ‡∏î‡πâ");
    } finally {
      setLoading(false);
    }
  };

  const handleCancelEdit = () => {
    setEditId(null);
    // --- 9. ‡∏õ‡∏£‡∏±‡∏ö‡∏Å‡∏≤‡∏£ Reset Form ---
    setForm({ bandName: '', albumTitle: '', songTitle: '', youtubeLink: '', albumId: '', trackNumber: '' });
    setError(null);
  };

  return (
    <div className="max-w-4xl mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">üé∂ ‡∏à‡∏±‡∏î‡∏Å‡∏≤‡∏£‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÄ‡∏û‡∏•‡∏á (Admin)</h1>
    
      {error && ( <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4" role="alert"><p>{error}</p></div> )}

      {/* --- 10. ‡∏õ‡∏£‡∏±‡∏ö Form Inputs --- */}
      <div className="bg-white rounded-xl shadow p-6 mb-6">
        <h2 className="text-xl font-semibold mb-3">{editId ? '‚úèÔ∏è ‡πÅ‡∏Å‡πâ‡πÑ‡∏Ç‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÄ‡∏û‡∏•‡∏á' : '‚ûï ‡πÄ‡∏û‡∏¥‡πà‡∏°‡πÄ‡∏û‡∏•‡∏á‡πÉ‡∏´‡∏°‡πà'}</h2>
        {/* Row 1: Band, Album Title, Album ID */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-3">
          <input
            type="text" name="bandName" placeholder="‡∏ä‡∏∑‡πà‡∏≠‡∏ß‡∏á‡∏î‡∏ô‡∏ï‡∏£‡∏µ"
            value={form.bandName} onChange={handleChange}
            className="border p-2 rounded w-full" required disabled={loading}
          />
          <input
            type="text" name="albumTitle" placeholder="‡∏ä‡∏∑‡πà‡∏≠‡∏≠‡∏±‡∏•‡∏ö‡∏±‡πâ‡∏°"
            value={form.albumTitle} onChange={handleChange}
            className="border p-2 rounded w-full" required disabled={loading} 
          />
          <input
            type="text" name="albumId" placeholder="Album ID (‡πÄ‡∏ä‡πà‡∏ô onlymonday_album_1)" // <--- Input ‡∏™‡∏≥‡∏´‡∏£‡∏±‡∏ö Album ID
            value={form.albumId} onChange={handleChange}
            className="border p-2 rounded w-full" required disabled={loading}
          />
        </div>
        {/* Row 2: Song Title, Track Number */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-3">
          <input
            type="text" name="songTitle" placeholder="‡∏ä‡∏∑‡πà‡∏≠‡πÄ‡∏û‡∏•‡∏á"
            value={form.songTitle} onChange={handleChange}
            className="border p-2 rounded w-full" required disabled={loading}
          />
          <input
            type="number" name="trackNumber" placeholder="Track Number" // <--- Input ‡∏™‡∏≥‡∏´‡∏£‡∏±‡∏ö Track Number
            value={form.trackNumber} onChange={handleChange}
            className="border p-2 rounded w-full" required min="1" disabled={loading}
          />
        </div>
        {/* Row 3: YouTube Link */}
        <input
          type="url" name="youtubeLink" placeholder="‡∏•‡∏¥‡∏á‡∏Å‡πå YouTube (https://...)"
          value={form.youtubeLink} onChange={handleChange}
          className="border p-2 rounded w-full mb-4" required disabled={loading}
        />
        <div className="flex space-x-2 justify-end">
          {editId ? (
            <>
              <button onClick={handleUpdate} className="bg-yellow-500 hover:bg-yellow-600 text-white px-4 py-2 rounded transition" disabled={loading}>
                {loading ? '‡∏Å‡∏≥‡∏•‡∏±‡∏á‡∏≠‡∏±‡∏õ‡πÄ‡∏î‡∏ï...' : '‡∏≠‡∏±‡∏õ‡πÄ‡∏î‡∏ï'}
              </button>
              <button onClick={handleCancelEdit} className="bg-gray-500 hover:bg-gray-600 text-white px-4 py-2 rounded transition" disabled={loading}>
                ‡∏¢‡∏Å‡πÄ‡∏•‡∏¥‡∏Å
              </button>
            </>
          ) : (
            <button onClick={handleAdd} className="bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded transition" disabled={loading}>
              {loading ? '‡∏Å‡∏≥‡∏•‡∏±‡∏á‡πÄ‡∏û‡∏¥‡πà‡∏°...' : '‡πÄ‡∏û‡∏¥‡πà‡∏°‡πÄ‡∏û‡∏•‡∏á'}
            </button>
          )}
        </div>
      </div>
      

      {/* --- 11. ‡∏õ‡∏£‡∏±‡∏ö‡∏Å‡∏≤‡∏£‡πÅ‡∏™‡∏î‡∏á‡∏ú‡∏• List --- */}
      <div className="bg-white rounded-xl shadow p-6">
        <h2 className="text-xl font-semibold mb-4">üìÑ ‡∏£‡∏≤‡∏¢‡∏Å‡∏≤‡∏£‡πÄ‡∏û‡∏•‡∏á‡∏ó‡∏±‡πâ‡∏á‡∏´‡∏°‡∏î</h2>
        {loading && songs.length === 0 ? ( <p className="text-gray-500">‡∏Å‡∏≥‡∏•‡∏±‡∏á‡πÇ‡∏´‡∏•‡∏î‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•...</p> )
         : songs.length === 0 ? ( <p className="text-gray-500">‡∏¢‡∏±‡∏á‡πÑ‡∏°‡πà‡∏°‡∏µ‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÄ‡∏û‡∏•‡∏á‡πÉ‡∏ô‡∏£‡∏∞‡∏ö‡∏ö</p> )
         : (
          <ul className="divide-y divide-gray-200">
            {songs.map((song) => (
              <li key={song.id} className="py-4 flex flex-col md:flex-row justify-between md:items-start"> {/* items-start */}
                <div className="mb-2 md:mb-0 md:flex-1 md:mr-4 overflow-hidden"> {/* overflow-hidden */}
                  <p className="font-semibold text-lg truncate" title={song.songTitle}>{song.songTitle}</p> {/* truncate */}
                  <p className="text-sm text-gray-700">
                    <span className="font-medium">‡∏ß‡∏á:</span> {song.bandName}
                  </p>
                  <p className="text-sm text-gray-700">
                    <span className="font-medium">‡∏≠‡∏±‡∏•‡∏ö‡∏±‡πâ‡∏°:</span> {song.albumTitle}
                  </p>
                  <p className="text-sm text-gray-700">
                    <span className="font-medium">Album ID:</span> {song.albumId} {/* <--- ‡πÅ‡∏™‡∏î‡∏á albumId */}
                  </p>
                  <p className="text-sm text-gray-700">
                    <span className="font-medium">Track:</span> {song.trackNumber} {/* <--- ‡πÅ‡∏™‡∏î‡∏á trackNumber */}
                  </p>
                  {song.youtubeLink && (
                    <a href={song.youtubeLink} target="_blank" rel="noopener noreferrer"
                       className="text-sm text-blue-600 hover:text-blue-800 hover:underline break-all block mt-1"> {/* block mt-1 */}
                       üîó ‡∏•‡∏¥‡∏á‡∏Å‡πå YouTube
                    </a>
                  )}
                </div>
                <div className="flex space-x-3 self-start md:self-center mt-2 md:mt-0"> {/* mt-2 md:mt-0 */}
                  <button onClick={() => handleEdit(song)} className="text-yellow-600 hover:text-yellow-800 hover:underline text-sm" disabled={loading || !!editId}>
                    ‡πÅ‡∏Å‡πâ‡πÑ‡∏Ç
                  </button>
                  <button onClick={() => handleDelete(song.id)} className="text-red-600 hover:text-red-800 hover:underline text-sm" disabled={loading || !!editId}>
                    ‡∏•‡∏ö
                  </button>
                </div>
              </li>
            ))}
          </ul>
        )}
      </div>
    </div>
  );
}